.header {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  &-top {
    padding: 25px 0;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__left {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__right {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__logo {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      max-width: 300px;
      img {
        width: 100%;
      }
     img:nth-child(2) {
       margin-top: 15px;
     }
    }
    &__info {
      margin-left: 40px;
    }
    &__location {
      display: flex;
      align-items: center;
      &-text {
        font-size: 16px;
        line-height: 120%;
        font-weight: 600;
        color: #000858;
        margin-left: 10px;
      }
    }
    &__mail {
      display: flex;
      align-items: center;
      margin-top: 10px;
      span {
        font-size: 16px;
        line-height: 120%;
        font-weight: 600;
        color: #000858;
        margin-left: 10px;
      }
    }
    &__number {
      font-size: 24px;
      line-height: 105%;
      font-weight: 600;
      color: #000858;
      display: flex;
      align-items: center;
      position: relative;
      &:not(:first-child) {
        margin-top: 9px;
      }
      i {
        position: absolute;
        top: 50%;
        left: -31px;
        transform: translate(0, -50%);
      }
    }
    &__btns {
      display: flex;
      align-items: center;
      margin-left: 40px;
    }
    &__btn {
      width: 177px;
      height: 48px;
    }
    &__btn2 {
      width: 225px;
      height: 48px;
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      color: #ffffff;
      background-color: #4caf50;
      border-radius: 8px;
      margin-left: 30px;
    }
  }
  &-bottom {
    &__inner {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border: 1px solid #d3e3ec;
      border-radius: 8px;
      padding: 25px;
    }
    &__list {
      display: flex;
      align-items: center;
      li {
        font-size: 16px;
        line-height: 120%;
        font-weight: 400;
        color: #000858;
        margin-left: 58px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: -28px;
          transform: translate(0, -50%);
          width: 1px;
          height: 24px;
          background: #000858;
        }
      }
    }
    &__item {
      position: relative;
      &2 {
        display: none;
      }
      &--active {
        .header-bottom__item-hidden {
          opacity: 1;
          pointer-events: all;
        }
      }
      &-show {
        display: flex;
        align-items: center;
        cursor: pointer;
        span {
          font-size: 16px;
          font-weight: 400;
          color: #000858;
          margin-right: 10px;
        }
      }
      &-hidden {
        position: absolute;
        top: 100%;
        left: 0;
        min-width: 200px;
        padding-top: 30px;
        opacity: 0;
        pointer-events: none;
      }
    }
    &__hidden-inner {
      background: #ffffff;
      border: 1px solid #d3e3ec;
      border-radius: 8px;
      padding: 15px;
    }
    &__hidden-link {
      font-size: 16px;
      font-weight: 400;
      color: #000858;
      display: block;
      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
}

@media screen and (max-width: 1420px) {
  .header {
    &-top {
      &__logo {
        width: 200px;
      }
      &__btns {
        margin-left: 25px;
      }
      &__btn2 {
        margin-left: 15px;
      }
    }
    &-bottom {
      &__inner {
        padding: 25px;
      }
      &__list {
        li {
          margin-left: 38px;
          &:after {
            left: -19px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &-top {
      &__location {
        span {
          display: block;
        }
      }
      &__btns {
        display: block;
        margin-left: 50px;
      }
      &__btn {
        width: 225px;
      }
      &__btn2 {
        margin: 10px 0 0 0;
      }
    }
    &-bottom {
      &__list {
        li {
          &:nth-child(6) ~ li {
            display: none;
          }
        }
      }
      &__item2 {
        display: block;
      }
      &__item {
        &-show2 {
          span {
            width: 4px;
            height: 4px;
            background-color: #000858;
            margin-right: 0;
            &:not(:first-child) {
              margin-left: 4px;
            }
          }
        }
        &-hidden2 {
          left: unset;
          right: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 1080px) {
  .header {
    &-top {
      &__info {
        margin-left: 30px;
      }
      &__btns {
        margin-left: 30px;
      }
    }
    &-bottom {
      &__list {
        li {
          font-size: 14px;
        }
      }
      &__item {
        &-show {
          span {
            font-size: 14px;
          }
        }
      }
      &__hidden-link {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .header {
    &-top {
      &__inner {
        align-items: start;
      }
      &__left {
        align-items: start;
      }
      &__burger {
        width: 24px;
        height: 16px;
        position: relative;
        z-index: 6;
        margin-top: 5px;
        margin-right: 35px;
        cursor: pointer;
        &--active {
          span:nth-child(1) {
            transform: rotate(-45deg);
            top: 50% !important;
          }
          span:nth-child(2) {
            display: none;
          }
          span:nth-child(3) {
            transform: rotate(45deg);
            top: 50%;
          }
        }
        span {
          position: absolute;
          left: 0;
          width: 100%;
          height: 1px;
          background-color: #c90608;
          transition: all 0.5s;
        }
        span:nth-child(1) {
          top: 0;
        }
        span:nth-child(2) {
          top: 50%;
          transform: translate(0, -50%);
        }
        span:nth-child(3) {
          bottom: 0;
        }
      }
      &__info {
        display: none;
      }
      &__number {
        font-size: 20px;
      }
      &__btns {
        margin-left: 30px;
      }
    }
    &-bottom {
      &__inner {
        position: absolute;
        z-index: 5;
        top: 0;
        left: -105%;
        width: 100%;
        height: 100vh;
        flex-direction: column;
        justify-content: start;
        align-items: start;
        background: #ffffff;
        border: unset;
        padding: 100px 15px 15px;
        transition: all 0.5s;
        &--active {
          left: 0;
        }
      }
      &__list {
        display: block;
        li {
          font-size: 16px;
          margin: 15px 0 0 0;
          &:nth-child(6) ~ li {
            display: block;
          }
          &:after {
            display: none;
          }
        }
      }
      &__item {
        &--active {
          .header-bottom__item-hidden {
            display: block;
          }
        }
        &2 {
          display: none;
        }
        &-hidden {
          position: static;
          padding-top: 10px;
          opacity: 1;
          pointer-events: all;
          display: none;
        }
      }
      &__hidden-inner {
        font-size: 16px;
        background: transparent;
        border: unset;
        padding: 0 0 0 15px;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .header {
    &-top {
      &__inner {
        align-items: center;
      }
      &__burger {
        margin-top: 10px;
        margin-right: 15px;
      }
      &__logo {
        width: 150px;
        img:nth-child(2) {
          margin-top: 5px;
        }
      }
      &__numbers {
        display: none;
      }
      &__btns {
        display: flex;
        margin-left: 0;
      }
      &__btn {
        width: 150px;
      }
      &__btn2 {
        width: 190px;
        font-size: 14px;
        margin: 0 0 0 8px;
      }
    }
  }
}

@media screen and (max-width: 630px) {
  .header {
    &-top {
      &__inner {
        padding-left: 0;
      }
      &__inner {
        display: block;
      }
      &__right {
        margin-top: 15px;
      }
    }
  }
}

@media screen and (max-width: 399px) {
  .header {
    &-top {
      &__btn {
        width: 120px;
        font-size: 12px;
      }
      &__btn2 {
        width: 160px;
        font-size: 12px;
      }
    }
  }
}