.display2 {
  display: flex;
  align-items: center;
  span {
    position: relative;
    z-index: 1;
    font-size: 32px;
    line-height: 120%;
    font-weight: 600;
    color: #000858;
    margin-left: 35px;
  }
}

.btn-red {
  font-size: 16px;
  line-height: 120%;
  font-weight: 600;
  color: #ffffff;
  background-color: #c90608;
  border-radius: 8px;
}

.btn-green {
  font-size: 16px;
  line-height: 120%;
  font-weight: 600;
  color: #ffffff;
  background-color: #4caf50;
  border-radius: 8px;
}

.btn-blue {
  font-size: 16px;
  line-height: 120%;
  font-weight: 600;
  color: #ffffff;
  background-color: #4169e1;
  border-radius: 8px;
}

@media screen and (max-width: 767px) {
  .btn-red {
    font-size: 14px;
  }
  .btn-green {
    font-size: 14px;
  }
  .btn-blue {
    font-size: 14px;
  }
}

@media screen and (max-width: 400px) {
  .display2 {
    span {
      font-size: 24px;
      margin-left: 15px;
    }
  }
}