.content {
  position: relative;
}
.sidebar {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 80px;
  height: 100vh;
  background-color: rgba(0, 8, 88, 0.7);
  padding: 25px 0;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__menu {
    position: relative;
    width: 24px;
    height: 16px;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #ffffff;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
  }
  &__search {
    width: 21px;
    height: 21px;
    position: relative;
    margin-top: 25px;
    cursor: pointer;
    img {
      position: absolute;
      top: 0;
      left: 0;
    }
    input {
      width: 100%;
      height: 100%;
      border: none;
      margin: 0;
      padding: 0;
      background: transparent;
    }
  }
  &__socials {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 25px;
  }
  &__social {
    &:not(:first-child) {
      margin-top: 25px;
    }
  }
  &__btns {
    margin-top: 96px;
  }
  &__btn {
    width: 48px;
    height: 131px;
    span {
      writing-mode: vertical-rl;
      transform: rotate(180deg);
    }
  }
  &__btn2 {
    width: 48px;
    height: 146px;
    margin-top: 32px;
    span {
      writing-mode: vertical-rl;
      transform: rotate(180deg);
    }
  }
  &__btn3 {
    width: 48px;
    height: 161px;
    margin-top: 32px;
    span {
      writing-mode: vertical-rl;
      transform: rotate(180deg);
    }
  }
}
.main {
  &__inner {
    position: relative;
  }
  &__slide-inner {
    max-width: 565px;
    margin: 0 auto;
    text-align: center;
  }
  &__title {
    font-size: 64px;
    line-height: 120%;
    font-weight: 700;
    color: #000858;
  }
  &__desc {
    font-size: 24px;
    line-height: 120%;
    font-weight: 400;
    color: #000858;
    margin-top: 30px;
  }
  &__btn {
    width: 162px;
    height: 64px;
    margin: 25px auto 0;
  }
  .swiper-slide {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 305px 0 230px;
  }
  .swiper-button-prev {
    z-index: 1;
    left: 150px;
    width: 56px;
    height: 56px;
    background: #ffffff;
    border: 1px solid #c5c5c5;
    border-radius: 8px;
    img {
      transition: all 0.3s;
    }
    &:hover {
      img {
        transform: scale(1.3);
      }
    }
    &:after {
      display: none;
    }
  }
  .swiper-button-next {
    z-index: 1;
    right: 150px;
    width: 56px;
    height: 56px;
    background: #ffffff;
    border: 1px solid #c5c5c5;
    border-radius: 8px;
    img {
      transition: all 0.3s;
    }
    &:hover {
      img {
        transform: scale(1.3);
      }
    }
    &:after {
      display: none;
    }
  }
  .swiper-pagination {
    z-index: 1;
    bottom: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    &-bullet {
      width: 8px;
      height: 8px;
      background-color: #000858;
      opacity: 1;
    }
    &-bullet-active {
      width: 32px;
      height: 32px;
      background-color: rgba(0, 8, 88, 0.4);;
      border-radius: 50%;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: #ffffff;
      }
    }
  }
}
.sea {
  padding: 130px 0 60px;
  position: relative;
  overflow: hidden;
  &__img-bg {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
  }
  &__img-bg2 {
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &__img-map {
    position: absolute;
    top: -80px;
    right: 0;
  }
  &__inner {
    position: relative;
  }
  &__top {
    display: flex;
    align-items: center;
  }
  &__btn {
    width: 352px;
    height: 64px;
    margin-left: 30px;
  }
  &__subtitle {
    display: flex;
    align-items: center;
    margin: 40px 0 0 0;
    i {
      width: 40px;
      img {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 24px;
      line-height: 120%;
      font-weight: 600;
      color: #000858;
      margin-left: 25px;
    }
  }
  .swiper {
    overflow: unset;
    margin-top: 55px;
  }
  @media screen and (min-width: 575px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4) - (40px * 3 / 4)));
      gap: 40px;
      transform: unset !important;
    }
  }
  &__card {
    position: relative;
    &-bg {
      width: 100%;
    }
    &-title {
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      color: #000000;
      text-align: center;
      padding: 30px;
    }
  }
}
.tours {
  padding: 60px 0 120px;
  position: relative;
  min-height: 1644px;
  &__img-bg {
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__tabs {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__tab {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 80px;
    border: 1px solid #c5c5c5;
    border-radius: 8px 0 0 8px;
    background: #ebebeb;
    border-right: unset;
    cursor: pointer;
    span {
      margin-left: 20px;
    }
    &2 {
    border-radius: 0 8px 8px 0;
      border-right: 1px solid #c5c5c5;
  }
    &--active {
      background: #ffffff;
    }
  }
  .swiper {
    overflow: unset;
  }
  @media screen and (min-width: 575px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4) - (40px * 3 / 4)));
      gap: 40px;
      transform: unset !important;
    }
  }
  &__content {
    margin-top: 55px;
    display: none;
    &--active {
      display: block;
    }
  }
  &__card {
    background: #ffffff;
    border: 1px solid #c5c5c5;
    transition: all 0.5s;
    position: relative;
    &:hover {
      border: 1px solid transparent;
      box-shadow: 4px 25px 38px 0px rgba(0,0,0,0.2);
      .tours__card-img {
        img {
          transform: scale(1.2);
        }
      }
      .tours__card-btn {
        background-color: #c90608;
        color: #ffffff;
      }
    }
    &-tag {
      position: absolute;
      z-index: 1;
      top: 15px;
      left: 15px;
      background-color: #c90608;
      border-radius: 4px;
      font-size: 16px;
      line-height: 105%;
      font-weight: 600;
      color: #ffffff;
      padding: 10px;
    }
    &-tag2 {
      position: absolute;
      z-index: 1;
      top: 15px;
      left: 15px;
      background-color: #000858;
      border-radius: 4px;
      font-size: 16px;
      line-height: 105%;
      font-weight: 600;
      color: #ffffff;
      padding: 10px;
    }
    &-img {
      overflow: hidden;
      img {
        width: 100%;
        height: auto;
        transition: all 0.5s;
      }
    }
    &-desc {
      border-radius: 0 0 8px 8px;
      background: #ffffff;
      padding: 30px;
    }
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 400;
      color: #000858;
    }
    &-city {
      font-size: 16px;
      line-height: 105%;
      font-weight: 400;
      color: #666666;
      border: 1px solid #c5c5c5;
      border-radius: 4px;
      padding: 5px 25px;
      display: inline-block;
      margin-top: 15px;
    }
    &-info {
      margin-top: 25px;
    }
    &-price {
      margin-top: 25px;
      span {
        font-size: 24px;
        line-height: 105%;
        font-weight: 600;
        color: #000000;
        margin-left: 10px;
      }

    }
    &-btn {
      width: 100%;
      height: 64px;
      background: transparent;
      border: 1px solid #c90608;
      border-radius: 8px;
      margin-top: 25px;
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      color: #c90608;
    }
  }
  &__info-desc {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-top: 10px;
    }
    span {
      font-size: 14px;
      line-height: 105%;
      font-weight: 400;
      color: #666666;
      margin-left: 15px;
    }
  }
  &__btn {
    width: 303px;
    height: 64px;
    margin: 55px auto 0;
  }
}
