@media screen and (max-width: 767px) {
  .main {
    &__slide-inner {
      max-width: 365px;
    }
    .swiper-slide {
      padding: 275px 0 230px;
    }
    .swiper-button-next {
      right: 0;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-pagination {
      bottom: 140px;
    }
  }
  .sea {
    .swiper-wrapper {
      grid-template-columns: repeat(2,calc((100% / 2) - (15px * 1 / 2)));
    }
  }
  .tours {
    &__tab {
      span {
        margin-left: 15px;
      }
    }
    &__content {
      margin-top: 40px;
    }
