      grid-template-columns: repeat(2,calc((100% / 2) - (15px * 1 / 2)));
    }
    &__btn {
      margin-top: 40px;
    }
  }
  .about {
    &__inner {
      margin-top: 50px;
    }
    &__left {
      padding: 30px;
    }
    &__items {
      grid-template-columns: 1fr 1fr;
      margin-top: 65px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 47%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 2px;
        background: #c5c5c5;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 2px;
        height: 100%;
        background: #c5c5c5;
      }
    }
    &__item {
      &:not(:last-child) {
        &:after {
          display: none;
        }
      }
    }
  }
  .news {
    &__card-btn {
      margin: 45px auto;
    }
    &__btn {
      width: 254px;
    }
