.footer {
  background-color: #000858;
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    display: block;
    width: 259px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__nav {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    margin: 0 30px;
  }
  &__list {
    li {
      font-size: 16px;
      line-height: 105%;
      font-weight: 400;
      color: #ffffff;
      &:not(:first-child) {
        margin: 15px 0 0 0;
      }
    }
  }
  &__brevis {
    display: flex;
    align-items: center;
    &-text {
      font-size: 12px;
      line-height: 120%;
      font-weight: 400;
      color: #ffffff;
      margin-left: 20px;
      a {
        display: block;
      }
    }
  }
  &__socials {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 25px;
  }
  &__social {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-left: 10px;
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 400;
      color: #ffffff;
      margin-left: 10px;
    }
  }
  &__btn {
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
    color: #ffffff;
    width: 100%;
    height: 64px;
    border: 1px solid rgba(255, 255, 255, 0.2);;
    border-radius: 8px;
    margin-top: 25px;
  }
}

@media screen and (max-width: 1420px) {
  .footer {
    &__inner {
      flex-direction: column;
      position: relative;
    }
    &__logo {
      width: 180px;
      position: absolute;
      left: 0;
      bottom: 8px;
    }
    &__nav {
      margin: 0 0 40px;
    }
    &__info {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      padding-left: 200px;
    }
    &__socials {
      order: -2;
      margin-top: 0;
    }
    &__btn {
      width: 256px;
      order: -1;
      margin: 0 15px;
    }
  }
}

@media screen and (max-width: 1050px) {
  .footer {
    &__social {
      span {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .footer {
    padding: 30px 0;
    &__list {
      li {
        font-size: 14px;
      }
      li:nth-child(1) {
        font-size: 16px;
      }
    }
    &__btn {
      width: 190px;
      height: 49px;
      font-size: 12px;
      margin: 0 10px;
    }
    &__brevis {
      &-text {
        margin-left: 10px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .footer {
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: unset;
    }
    &__logo {
      width: 200px;
      position: static;
    }
    &__nav {
      display: none;
    }
    &__info {
      padding-left: 0;
    }
    &__socials {
      display: none;
    }
    &__btn {
      display: none;
    }
  }
}

@media screen and (max-width: 575px) {
  .footer {
    &__inner {
      flex-direction: column;
    }
    &__info {
      margin-top: 20px;
    }
  }
}